@import url(https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600;700;800;900&display=swap);
@media all and (min-width: 480px) {
  .Login {
    padding: 60px 0;
  }

  .Login form {
    margin: 0 auto;
    max-width: 320px;
  }
}

.sidebar {
  display: flex;
  flex-direction: column;
  flex: 0.15 1;
  height: 100vh;
  background-color: #2f3135;
}

.sidebar_top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  background-color: #2f3135;
  color: white;
  border-bottom: 3px solid #26282c;
}

.sidebar_channel {
  flex: 1 1;
}
.sidebar_addChannel {
  cursor: pointer;
}
.sidebar_addChannel:hover {
  color: white;
}
.sidebar_channelheader {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  background-color: #2f3135;
  color: gray;
}
.sidebar_header {
  display: flex;
  align-items: center;
}

.sidebarChannel > h4:hover {
  color: rgb(194, 165, 194);
  background-color: #40464b;
}

.sidebarChannel > h4 {
  display: flex;
  padding-left: 15px;
  align-items: center;
  background-color: #2f3135;
  color: gray;
  cursor: pointer;
}

.sidebarChannel_hash {
  font-size: 30px;
  padding: 8px;
}

.community {
  display: flex;
}

.chat {
  display: flex;
  flex-direction: column;
  flex: 0.85 1;
  background-color: #363a3f;
  height: 100vh;
}
.chat_messages {
  flex: 1 1;
  overflow: scroll;
}
.chat_messages::-webkit-scrollbar {
  display: none;
}
.chat_messages {
  -ms-overflow-style: none;
  scrollbar-width: none;
}

.chat_input {
  color: lightgray;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px;
  border-radius: 5px;
  margin: 20px;
  border-top: 1px solid gray;
  background-color: #474b53;
}

.chat_input > form > input {
  padding: 15px;
  background: transparent;
  border: none;
  width: 100%;
  outline-width: 0;
  color: white;
  font-size: large;
}
.chat_input > form {
  flex: 1 1;
}
.chat_sendbutton {
  display: none;
}

.chatheader {
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: grey;
  padding: 10px;
}
.chatheader_hash {
  font-size: 30px;
  padding: 10px;
}

.chatheader_left > h3 {
  display: flex;
  align-items: center;
}
.chatheader_right {
  display: flex;
  align-items: center;
  flex: 0.3 1;
  justify-content: space-between;
}
.chatheader_right > .MuiSvgIcon-root {
  cursor: pointer;
}

.chatheader_right > .MuiSvgIcon-root:hover {
  color: white;
}

.chatheader_search {
  display: flex;
  align-items: center;
  color: gray;
  background-color: #2f3135;
  padding: 3px;
}
.chatheader_search > input {
  background: transparent;
  outline-width: 0;
  color: white;
  border: none;
}
.backbutton {
  font-size: medium;
  cursor: pointer;
}

.message {
  display: flex;
  align-items: center;
  padding-left: 20px;
  color: white;
}

.message_info {
  margin-left: 20px;
}
.message_timestamp {
  color: grey;
  margin-left: 20px;
  font-size: x-small;
}
.message_head {
  font-size: medium;
}

#tickets {
  width: 500px;
  position: relative;
  margin: 20px auto;
  box-shadow: 4px 0 60px -30px #000;
  border-radius: 10px;
}
.ticket {
  width: 500px;
  height: 140px;
  background: #fff;
  border: 5px solid #e8ebf0;
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
}
.ticket-container {
  position: relative;
  overflow: hidden;
}
.matchtime {
  margin: 3px;
  color: rgb(92, 92, 92);
  font-size: 14px;
  font-weight: bold;
}
.teams {
  text-align: center;
}
.status {
  border-radius: 3px;
  text-align: center;
  position: relative;
  bottom: 0px;
  color: #fff;
}
.score {
  text-align: center;
}

.fixturewindow {
  display: flex;
  flex-wrap: wrap;
  background-color: #e1e6e2;
}
.fixturewindow > * {
  flex: 1 1;
}

.whole {
  display: flex;
}
.left {
  flex: 0.33 1;
}
.right {
  flex: 0.33 1;
}
.tablecontainer {
  flex: 0.33 1;
  background-color: var(--color-bg-primary);
  display: flex;
  flex-direction: column;
  min-width: 600px;
  border-radius: 10px;
  box-shadow: 0 0 2px 0 rgba(#303030, 0.1), 0 4px 4px 0 rgba(#303030, 0.1);
}

*,
*:after,
*:before {
  box-sizing: border-box;
}

:root {
  --color-text-primary: #1c2a38;
  --color-text-secondary: #8a8f98;
  --color-text-alert: #d72641;
  --color-text-icon: #dbdade;
  --color-bg-primary: #fff;
  --color-bg-secondary: #f3f5f9;
  --color-bg-alert: #fdeaec;
  --color-theme-primary: #623ce6;
}

button,
input,
select,
textarea {
  font: inherit;
}

img {
  display: block;
}

strong {
  font-weight: 600;
}

body {
  font-family: "Inter", sans-serif;
  line-height: 1.5;
  color: #1c2a38;
  color: var(--color-text-primary);
  background-color: #f3f5f9;
  background-color: var(--color-bg-secondary);
}

.match {
  background-color: #fff;
  background-color: var(--color-bg-primary);
  display: flex;
  flex-direction: column;
  min-width: 600px;
  border-radius: 10px;
  box-shadow: 0 0 2px 0 rgba(#303030, 0.1), 0 4px 4px 0 rgba(#303030, 0.1);
}

.match-header {
  display: flex;
  padding: 16px;
  border-bottom: 2px solid rgba(#303030, 0.1);
}

.match-status {
  background-color: #fdeaec;
  background-color: var(--color-bg-alert);
  color: #d72641;
  color: var(--color-text-alert);
  padding: 8px 12px;
  border-radius: 6px;
  font-weight: 600;
  font-size: 14px;
  display: flex;
  align-items: center;
  line-height: 1;
  margin-right: auto;
  &:before {
    content: "";
    display: block;
    width: 6px;
    height: 6px;
    background-color: currentcolor;
    border-radius: 50%;
    margin-right: 8px;
  }
}

.match-tournament {
  display: flex;
  align-items: center;
  font-weight: 600;
  img {
    width: 20px;
    margin-right: 12px;
  }
}

.match-actions {
  display: flex;
  margin-left: auto;
}

.btn-icon {
  border: none;
  background-color: transparent;
  color: #dbdade;
  color: var(--color-text-icon);
  display: flex;
  align-items: center;
  justify-content: center;
}

.match-content {
  display: flex;
  position: relative;
}

.column {
  padding: 32px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: calc(100% / 3);
}

.team {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.team-logo {
  width: 100px;
  height: 100px;
  max-width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  background-color: #fff;
  background-color: var(--color-bg-primary);
  box-shadow: 0 4px 4px 0 rgba(#303030, 0.15),
    0 0 0 15px #f3f5f9;
  box-shadow: 0 4px 4px 0 rgba(#303030, 0.15),
    0 0 0 15px var(--color-bg-secondary);
  img {
    width: 50px;
  }
}

.team-name {
  text-align: center;
  margin-top: 24px;
  font-size: 20px;
  font-weight: 600;
}

.match-details {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.match-date,
.match-referee {
  font-size: 14px;
  color: #8a8f98;
  color: var(--color-text-secondary);
  strong {
    color: var (--color-text-primary);
  }
}

.match-score {
  margin-top: 12px;
  display: flex;
  align-items: center;
}

.match-score-number {
  font-size: 48px;
  font-weight: 600;
  line-height: 1;
  &--leading {
    color: #623ce6;
    color: var(--color-theme-primary);
  }
}

.match-score-divider {
  font-size: 28px;
  font-weight: 700;
  line-height: 1;
  color: var (--color-text-icon);
  margin-left: 10px;
  margin-right: 10px;
}

.match-time-lapsed {
  color: #df9443;
  font-size: 14px;
  font-weight: 600;
  margin-top: 8px;
}

.match-referee {
  margin-top: 12px;
}

.match-bet-options {
  display: flex;
  margin-top: 8px;
  padding-bottom: 12px;
}

.match-bet-option {
  margin-left: 4px;
  margin-right: 4px;
  border: 1px solid #dbdade;
  border: 1px solid var(--color-text-icon);
  background-color: #f9f9f9;
  border-radius: 2px;
  color: #8a8f98;
  color: var(--color-text-secondary);
  font-size: 14px;
  font-weight: 600;
  padding: 4px 8px;
}

.match-bet-place {
  position: absolute;
  bottom: -16px;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  border: none;
  background-color: #623ce6;
  background-color: var(--color-theme-primary);
  border-radius: 6px;
  padding: 10px 48px;
  color: rgba(#fff, 0.9);
  font-size: 14px;
  box-shadow: 0 4px 8px 0 rgba(#303030, 0.25);
}

.container {
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  padding-top: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.recap {
  padding-left: 30px;
  padding-right: 30px;
}
.fixture {
  padding-left: 30px;
  padding-right: 30px;
}

